import { useState, useEffect } from "react";
import axios from "axios";

// Todo
type Todo = {
  id: number;
  content: string;
  done: boolean;
};

type useTodoReturn = {
  text: string;
  items: Todo[];
  setText: React.Dispatch<React.SetStateAction<string>>;
  onAdd: (event: React.MouseEvent<HTMLButtonElement>) => void;
  fetchData: () => Promise<void>;
  onDelete: (id: number) => void;
  Onreverse: (id: number) => void;
};

const useTodo = (): useTodoReturn => {
  //  state
  const [text, setText] = useState<string>("");

  // Todo state
  const [items, setItems] = useState<Todo[]>([]);
  // Todo
  const onAdd = async (
    event: React.MouseEvent<HTMLButtonElement>
  ): Promise<void> => {
    try {
      // Id
      let num: number = Math.round(Math.random() * 100000);

      //
      const endpoint = "http://localhost:3100/todo";
      // Todo
      const requestData: Todo = {
        id: num,
        content: text,
        done: false,
      };

      // 
      await axios.post(endpoint, requestData);
      // Todo
      setItems((prevItems) => [...prevItems, requestData]);
      // 
      setText("");
    } catch (error) {
      console.error("Error:", error);
    }
  };
  //
  const fetchData = async (): Promise<void> => {
    try {
      //
      const response = await axios.get("http://localhost:3100/todo");
      console.log(response.data);
      // 
      const newData: Todo[] = response.data;
      //Todo
      setItems(newData);
    } catch (error) {
      console.error("Error fetching data:", error);
      // setItems 
      setItems([]);
    }
  };

  // todo.done
  const Onreverse = (id: number): void => {
    setItems((prevItems) =>
      prevItems.map((item) =>
        item.id === id ? { ...item, done: !item.done } : item
      )
    );
  };

  const onDelete = (id: number): void => {
    axios
      .delete(`http://localhost:3100/todo/${id}`)
      .then((response) => {
        console.log("", response.data);
      })
      .catch((error) => {
        console.error("", error);
      });
    setItems((prevItems) => prevItems.filter((item) => item.id !== id));
  };

  return {
    text,
    items,
    setText,
    onAdd,
    fetchData,
    onDelete,
    Onreverse,
  };
};

export default useTodo;
export type { Todo };
